import { studentApi } from "./studentApi";
import {
  studentStatsClassListFunction,
  studentStatsOneClassStudentListFunction,
  studentStatsLectureWiseStudentListFunction,
  studentStatsLectureWiseOneStudentSubjectListFunction,
  studentProfileAttendanceStatsDayWiseFunction,
  studentProfileAttendanceStatsLectureWiseFunction,
  studentDateWiseTimetableFunction,
} from "./student_manager_query_mutation_function";

const studentApiDynamic = studentApi.injectEndpoints({
  endpoints: (builder) => ({
    studentStatsClassList: studentStatsClassListFunction(builder),
    studentStatsOneClassStudentList:
      studentStatsOneClassStudentListFunction(builder),
    studentStatsLectureWiseStudentList:
      studentStatsLectureWiseStudentListFunction(builder),
    studentStatsLectureWiseOneStudentSubjectList:
      studentStatsLectureWiseOneStudentSubjectListFunction(builder),
    studentProfileAttendanceStatsDayWise:
      studentProfileAttendanceStatsDayWiseFunction(builder),
    studentProfileAttendanceStatsLectureWise:
      studentProfileAttendanceStatsLectureWiseFunction(builder),
    studentDateWiseTimetable: studentDateWiseTimetableFunction(builder),
    // staffTeachingPlanStatsFunction: staffTeachingPlanStatsFunction(builder),
  }),
});

export const {
  useStudentStatsClassListQuery,
  useStudentStatsOneClassStudentListQuery,
  useStudentStatsLectureWiseStudentListQuery,
  useStudentStatsLectureWiseOneStudentSubjectListQuery,
  useStudentProfileAttendanceStatsDayWiseQuery,
  useStudentProfileAttendanceStatsLectureWiseQuery,
  useStudentDateWiseTimetableQuery,
} = studentApiDynamic;
